<!-- App.vue -->

<template>
  <div>
    <CoinDisplay :tenCoins="tenCoins" />
  </div>
</template>

<script>
import { ref, computed } from 'vue';
import { tenCoins } from '../components/coin/Coin.js';
import CoinDisplay from '../components/coin/CoinDisplay.vue';
import '../components/coin/Coin.css';
import { fa } from '@fortawesome/free-solid-svg-icons'; // 导入正确的 FontAwesome 图标
import { FontAwesomeIcon as FaIcon } from '@fortawesome/vue-fontawesome';

export default {
  setup() {
    const coins = ref(tenCoins);
    const searchQuery = ref('');

    const matchedCoins = computed(() => {
      return coins.value.filter((coin) => coin.name.toLowerCase().includes(searchQuery.value.toLowerCase()));
    });

    return { searchQuery, matchedCoins };
  },
  components: {
    CoinDisplay, // 注册 CoinDisplay 组件
    fa,
    FaIcon
  }
};
</script>
